@import '../../styles/theme.scss';
@import '../../styles/breakpoints.scss';

.card {
  width: calc(50% - 3rem);
  padding: 2rem;
  border: 0.1rem solid $text-main;
  margin-bottom: 5rem;

  &__image {
    height: 40rem;
    margin-bottom: 2rem;
  }

  &__title {
    text-align: center;
    margin-bottom: 2rem;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }

  &__tags {
    text-align: center;
    opacity: 0.7;
    margin-bottom: 2rem;
  }

  &__input {
    border: 0.1rem solid $text-main;
    border-radius: 0.8rem;
    padding: 2rem;
    text-align: center;
    margin-bottom: 2rem;
  }

  &__btn {
    margin: 0 auto;
  }
}

@media only screen and (max-width: $breakpoint-tablet) {
  .card {
    padding: 1.7rem;
    margin-bottom: 3.8rem;

    &__image {
      height: 30rem;
    }
  }
}

@media only screen and (max-width: $breakpoint-mobile) {
  .card {
    padding: 1.4rem;
    margin-bottom: 2.6rem;

    &__image {
      height: 15rem;
    }
  }
}
